"use client"
import { useState, useEffect } from "react"
import { Bell, CheckCheck, Filter, Plus, Trash2 } from "lucide-react"
import { useStore } from "@/store"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { cn } from "@/lib/utils"
import { RoleUser } from "@/types/enums"
import { useToast } from "@/hooks/use-toast"

export default function NotificationsPage() {
  const { toast } = useToast()

  const [notifications, setNotifications] = useState<Notification[]>([])
  const [loading, setLoading] = useState(true)
  const [activeTab, setActiveTab] = useState<string>("all")
  const [typeFilter, setTypeFilter] = useState<string>("all")

  const loadNotifications = async () => {
    if (!user) return

    try {
      setLoading(true)
      const data = await fetchNotifications(user.role as RoleUser, user.user_id)
      setNotifications(data)
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to load notifications",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    loadNotifications()
  }, [user, toast])

  const handleViewNotification = (notification: Notification) => {
    setSelectedNotification(notification)
    openModal("viewNotification")
  }

  const handleMarkAllAsRead = async () => {
    if (!user) return

    try {
      await markAllNotificationsAsRead(user.role as RoleUser, user.user_id)
      toast({
        title: "Success",
        description: "All notifications marked as read",
      })
      loadNotifications()
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to mark notifications as read",
        variant: "destructive",
      })
    }
  }

  const handleDeleteNotification = async (id: number) => {
    try {
      await deleteNotification(id)
      setNotifications(notifications.filter((n) => n.id !== id))
      toast({
        title: "Success",
        description: "Notification deleted",
      })
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to delete notification",
        variant: "destructive",
      })
    }
  }

  const handleCreateNotification = () => {
    setSelectedNotification(null)
    openModal("createNotification")
  }

  // Filter notifications based on active tab and type filter
  const filteredNotifications = notifications.filter((notification) => {
    // Filter by status (tab)
    if (activeTab === "unread" && notification.status !== NotificationStatus.UNREAD) return false
    if (activeTab === "read" && notification.status !== NotificationStatus.READ) return false

    // Filter by type
    if (typeFilter !== "all" && notification.type !== typeFilter) return false

    return true
  })

  // Count notifications by status
  const unreadCount = notifications.filter((n) => n.status === NotificationStatus.UNREAD).length
  const readCount = notifications.filter((n) => n.status === NotificationStatus.READ).length

  // Check if user can create notifications
  const canCreateNotifications = user?.role === RoleUser.ADMIN || user?.role === RoleUser.STAFF

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    const now = new Date()
    const diffMs = now.getTime() - date.getTime()
    const diffMins = Math.round(diffMs / 60000)
    const diffHours = Math.round(diffMs / 3600000)
    const diffDays = Math.round(diffMs / 86400000)

    if (diffMins < 60) {
      return `${diffMins} minute${diffMins !== 1 ? "s" : ""} ago`
    } else if (diffHours < 24) {
      return `${diffHours} hour${diffHours !== 1 ? "s" : ""} ago`
    } else if (diffDays < 7) {
      return `${diffDays} day${diffDays !== 1 ? "s" : ""} ago`
    } else {
      return date.toLocaleDateString()
    }
  }

  const getNotificationTypeColor = (type: NotificationType) => {
    switch (type) {
      case NotificationType.SYSTEM:
        return "bg-purple-100 text-purple-800"
      case NotificationType.APPOINTMENT:
        return "bg-blue-100 text-blue-800"
      case NotificationType.VACCINE:
        return "bg-green-100 text-green-800"
      case NotificationType.FEEDBACK:
        return "bg-yellow-100 text-yellow-800"
      default:
        return "bg-gray-100 text-gray-800"
    }
  }

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">Notifications</h1>
        <div className="flex gap-2">
          {unreadCount > 0 && (
            <Button variant="outline" onClick={handleMarkAllAsRead}>
              <CheckCheck className="mr-2 h-4 w-4" />
              Mark all as read
            </Button>
          )}
          {canCreateNotifications && (
            <Button onClick={handleCreateNotification}>
              <Plus className="mr-2 h-4 w-4" />
              Create Notification
            </Button>
          )}
        </div>
      </div>

      <div className="grid gap-4 md:grid-cols-3">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Total Notifications</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center">
              <Bell className="mr-2 h-5 w-5 text-blue-600" />
              <div className="text-2xl font-bold">{notifications.length}</div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Unread Notifications</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center">
              <Bell className="mr-2 h-5 w-5 text-red-600" />
              <div className="text-2xl font-bold">{unreadCount}</div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Read Notifications</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="flex items-center">
              <Bell className="mr-2 h-5 w-5 text-green-600" />
              <div className="text-2xl font-bold">{readCount}</div>
            </div>
          </CardContent>
        </Card>
      </div>

      <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
        <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full sm:w-auto">
          <TabsList>
            <TabsTrigger value="all">All</TabsTrigger>
            <TabsTrigger value="unread">Unread</TabsTrigger>
            <TabsTrigger value="read">Read</TabsTrigger>
          </TabsList>
        </Tabs>

        <div className="flex items-center gap-2">
          <Filter className="h-4 w-4 text-muted-foreground" />
          <Select value={typeFilter} onValueChange={setTypeFilter}>
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Filter by type" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All Types</SelectItem>
              <SelectItem value={NotificationType.GENERAL}>General</SelectItem>
              <SelectItem value={NotificationType.SYSTEM}>System</SelectItem>
              <SelectItem value={NotificationType.APPOINTMENT}>Appointment</SelectItem>
              <SelectItem value={NotificationType.VACCINE}>Vaccine</SelectItem>
              <SelectItem value={NotificationType.FEEDBACK}>Feedback</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </div>

      <Card>
        <CardContent className="p-0">
          {loading ? (
            <div className="flex h-40 items-center justify-center">
              <p>Loading notifications...</p>
            </div>
          ) : filteredNotifications.length === 0 ? (
            <div className="flex h-40 flex-col items-center justify-center gap-2">
              <Bell className="h-8 w-8 text-muted-foreground" />
              <p className="text-muted-foreground">No notifications found</p>
            </div>
          ) : (
            <div className="divide-y">
              {filteredNotifications.map((notification) => (
                <div
                  key={notification.id}
                  className={cn(
                    "cursor-pointer p-4 transition-colors hover:bg-muted/50",
                    notification.status === NotificationStatus.UNREAD && "bg-blue-50",
                  )}
                  onClick={() => handleViewNotification(notification)}
                >
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                      <h3
                        className={cn("text-base", notification.status === NotificationStatus.UNREAD && "font-medium")}
                      >
                        {notification.title}
                      </h3>
                      <Badge className={getNotificationTypeColor(notification.type)}>{notification.type}</Badge>
                    </div>
                    <Button
                      variant="ghost"
                      size="icon"
                      className="h-8 w-8 text-muted-foreground hover:text-destructive"
                      onClick={(e) => {
                        e.stopPropagation()
                        handleDeleteNotification(notification.id)
                      }}
                    >
                      <Trash2 className="h-4 w-4" />
                      <span className="sr-only">Delete</span>
                    </Button>
                  </div>
                  <p className="mt-1 line-clamp-2 text-sm text-muted-foreground">{notification.message}</p>
                  <div className="mt-2 flex items-center justify-between text-xs text-muted-foreground">
                    <span>From: {notification.senderName}</span>
                    <span>{formatDate(notification.createdAt)}</span>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Modals */}
      <CreateNotificationModal />
      <ViewNotificationModal />
    </div>
  )
}

